<template>
	<HomeMenu :email="companyEmail" :mustFix="true" />
	<div class="container">
		<div class="topMargin"></div>
		<div class="backer">位置：<span @click="backHome">首页</span> / <span @click="backService">业务咨询</span></div>
		<div v-if="!showService" class="content">
			<div v-for="itemList in services" class="serviceCategory">
				<div class="subtitle">{{itemList.name}}</div>
				<div v-for="item in itemList.list" class="listitem" @click="goService(item.id)">{{item.name}}</div>
			</div>
			<div v-if="notdoneRequest.length>0" class="serviceCategory">
				<div class="subtitle">我提交的咨询</div>
				<div v-for="item in notdoneRequest" class="requestItem">
					<div class="cate"><span class="hint">业务类型：</span>{{item.category}}</div>
					<div class="ctx"><span class="hint">咨询业务内容：</span><br>{{item.content}}</div>
					<div class="stamp">{{item.submitAt}}</div>
				</div>
			</div>
			<div v-if="doneRequest.length>0" class="serviceCategory">
				<div class="subtitle">已回复的咨询</div>
				<div v-for="item in doneRequest" class="requestItem">
					<div class="cate"><span class="hint">业务类型：</span>{{item.category}}</div>
					<div class="ctx"><span class="hint">咨询业务内容：</span><br>{{item.content}}</div>
					<div class="ctx"><span class="hint">咨询回复：</span><br>{{item.reply}}</div>
					<div class="stamp">{{item.replyAt}} <span class="vice">({{item.submitAt}})</span></div>
				</div>
			</div>
		</div>
		<div v-if="showService" class="content">
			<div class="title">{{detailTitle}}</div>
			<div class="context">{{detailContent}}</div>
		</div>
		<div class="content">
			<div v-if="needLogin" class="login" @click="login">登录</div>
			<div v-if="!needLogin" class="userRequest">
				<div class="sepline"></div>
				<div class="hinttitle">提交业务咨询</div>
				<div class="sepline"></div>
				<div class="userName"><span class="hint">当前用户：</span>{{userName}}</div>
				<div class="userCompany"><span class="hint">所属公司：</span>{{userCompany}}</div>
				<div class="requestArea"><span class="hint">咨询详情：</span><div ref="RequestDetail" class="editor" contenteditable="true"></div></div>
				<div class="submitArea"><button @click="submitRequest">提交</button></div>
			</div>
		</div>
	</div>
	<FootMark />
	<QRCode />
</template>

<style scoped>
.menubar_container {
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100%;
}
.container .topMargin {
	padding-top: 60px;
}
.container .content {
	padding: 10px;
}
.container .backer {
	position: absolute;
	top: 70px;
	left: 10px;
	font-weight: bolder;
}
.container .backer > span {
	cursor: pointer;
}
.container .content > .serviceCategory > .subtitle {
	margin-top: 50px;
	padding-bottom: 10px;
	border-bottom: 1px dashed rgb(225, 225, 225);
	font-size: 20px;
	font-weight: bolder;
}
.container .content > .serviceCategory > .listitem {
	padding: 2px 5px;
	border-bottom: 1px dashed rgb(225, 225, 225);
	line-height: 24px;
	cursor: pointer;
}
.container .content > .login {
	position: absolute;
	top: 70px;
	right: 10px;
	font-weight: bolder;
	cursor: pointer;
}
.container .content > .title {
	margin-top: 50px;
	font-size: 20px;
	font-weight: bolder;
}
.container .content > .context {
	margin-top: 20px;
	margin-bottom: 20px;
}
.container .content > .userRequest {
	margin-top: 30px;
}
.container .content > .userRequest > div {
	line-height: 25px;
}
.container .content > .userRequest > div > span.hint {
	font-weight: bolder;
}
.container .content > .userRequest > div > div[contenteditable] {
	min-height: 80px;
	margin-top: 10px;
	padding: 10px;
	border-radius: 5px;
	border: 1px solid rgb(225, 225, 225);
	outline: none;
}
.container .content > .userRequest > div > div[contenteditable]:empty:before {
	content: '请输入想要咨询业务的具体情况（必选）';
	color: rgb(205, 205, 205);
	pointer-events: none;
}
.container .content > .userRequest .submitArea {
	margin-top: 10px;
	text-align: right;
}
.container .content > .userRequest .submitArea button {
	padding: 5px 10px;
	border-radius: 5px;
	border: 1px solid rgb(225, 225, 225);
	background-color: transparent;
	outline: none;
	cursor: pointer;
}
.container .content > .userRequest .hinttitle {
	margin-bottom: 10px;
	font-size: 24px;
	font-weight: bolder;
	text-align: center;
}
.container .content > .userRequest .sepline {
	height: 1px;
	margin-bottom: 10px;
	margin-left: -10px;
	margin-right: -10px;
	background-color: rgb(225, 225, 225);
}
.container .content > .serviceCategory > .requestItem {
	padding: 10px;
	border-bottom: 1px dashed rgb(225, 225, 225);
}
.container .content > .serviceCategory > .requestItem > div {
	line-height: 25px;
}
.container .content > .serviceCategory > .requestItem > div > span.hint {
	font-weight: bolder;
}
.container .content > .serviceCategory > .requestItem > div.stamp {
	font-size: 14px;
	color: rgb(105, 105, 105);
	text-align: right;
}
.container .content > .serviceCategory > .requestItem > div.stamp span.vice {
	color: rgb(175, 175, 175);
}
</style>

<script>
import HomeMenu from '@/components/homemenu.vue';
import FootMark from '@/components/footmark.vue';
import QRCode from '@/components/qrcode.vue';

var current;
EventMetro.sub('signin', (login, name, company) => {
	if (!current) return;
	current.onLogin(login, name, company);
});

export default {
	name: 'Services',
	components: {HomeMenu, FootMark, QRCode},
	data () {
		return {
			companyEmail: '',
			services: [],
			serviceName: {},
			detailTitle: '',
			detailContent: '',
			userCompany: '',
			userName: '',
			showService: false,
			currentService: '',
			needLogin: true,
			doneRequest: [],
			notdoneRequest: [],
		}
	},
	mounted () {
		current = this;
		var {hash, item} = this.$route.query || '', visited = false;
		if (!!hash) {
			visited = sessionStorage.get('visit-services-' + hash) || false;
		}
		else {
			visited = true;
			hash = '';
		}

		if (!item) {
			this.getServiceInfo(hash, visited);
		}
		else {
			this.goService(item);
		}
	},
	unmounted () {
		current = null;
	},
	methods: {
		async getServiceInfo (hash, visited) {
			this.currentService = '';
			this.showService = false;
			this.services.splice(0, this.services.length);

			var session = localStorage.get('sessionID') || '';
			this.showUserInfo(session);

			var info = await BackEnd.getServiceInfo(hash, visited, session);
			if (!info) {
				return;
			}

			this.companyEmail = info.company.email;
			var services = {};
			info.service.forEach(item => {
				this.serviceName[item.id] = item.name;
				var list = services[item.type];
				if (!list) {
					list = [];
					services[item.type] = list;
				}
				list.push(item);
			});
			for (let name in services) {
				this.services.push({
					name, list: services[name]
				});
			}

			this.showMyRequest(info.mine);
		},
		async goService (id) {
			this.currentService = id;
			this.showService = true;
			this.services.splice(0, this.services.length);

			var session = localStorage.get('sessionID') || '';
			this.showUserInfo(session);

			var info = await BackEnd.getServiceDetail(id);

			this.detailTitle = info.name;
			this.detailContent = info.detail;
		},
		async showUserInfo (session) {
			this.needLogin = true;

			var info = await BackEnd.getUserInfo(session);

			if (!!info.name) {
				this.needLogin = false;
				this.userName = info.name;
				this.userCompany = info.company;
			}
			else {
				this.needLogin = true;
			}
		},
		showMyRequest (list) {
			var notdones = [], dones = [];
			list.forEach(item => {
				item.category = this.serviceName[item.category] || item.category || '未指定类型';
				item.submitAt = stamp2string(item.submitAt);
				item.replyAt = stamp2string(item.replyAt);
				if (item.done) dones.push(item);
				else notdones.push(item);
			});
			notdones.sort((a, b) => b.submitAt - a.submitAt);
			dones.sort((a, b) => b.replyAt - a.replyAt);
			this.notdoneRequest.splice(0, this.notdoneRequest.length, ... notdones);
			this.doneRequest.splice(0, this.doneRequest.length, ... dones);
		},
		backHome () {
			var hash = this.$route.query.hash || '';
			this.$router.push({path: '/', query: {hash}});
		},
		backService () {
			if (!this.currentService) return;
			this.getServiceInfo('', true);
		},
		login () {
			EventMetro.pub('show_login');
		},
		onLogin (ok, name, company) {
			this.needLogin = false;
			this.userName = name;
			this.userCompany = company;
		},
		async submitRequest () {
			var session = localStorage.get('sessionID') || '';
			var content = this.$refs.RequestDetail.innerText;

			var result = await BackEnd.sendBusinessRequest(session, this.currentService, content);
			if (!result || !result.ok) return;
			this.$refs.RequestDetail.innerHTML = '';
		},
	}
}
</script>